import styled from "@emotion/styled";

export const StyleMovieItem = styled.div`
    position: relative;

    // display   
    display: flex; // flex  
    flex-direction: row; // align() justify()   
    align-items: center;
    
    width:100%;
    border: solid 1px #777; // , 1px, #777
    border-radius: 20px; // 
    // border-radius        
    overflow: hidden; //   hidden 
    box-shadow: 0px 3px 15px #0003; //    

    // scss,   margin-top  
    & + & {
        margin-top: 20px;
    }
`;

export const StyleMovieImageBox = styled.div`
    display: flex;
    width:40%;
    height: 100%;
`;

export const StyleMovieImage = styled.img`
    // display: block;  
    // block    ( )
    width:100%;
`;

export const StyleMovieContentBox = styled.div`
    width: 60%; //  40% 
    padding: 12px;
`;

export const StyleMovieAnchor = styled.a`
    color:#222;
    font-size: 12px;
    text-align: center; // text 
    word-break: keep-all; //    ()
    // break-all :   
    // keep-all :   

    text-decoration: none; //  X
    &:hover { //  hover(& this )
        text-decoration: underline; //  O
    }
`;

export const StyleMovieTitle = styled.h3`
    color: #000;
`;

export const StyleMovieSubtitle = styled.h4`
    color : #555;
    margin-top: 8px;
`;

export const StyleMovieDirector = styled.p`
    color: #999;
    margin-top: 40px;
`;

export const StyleMovieList = styled.div`
    padding: 0 80px; //  padding
`;

export const StyleMovieCheckboxArea = styled.div`
    position: absolute;

    top: 6px;
    right: 6px;
`;